#header {
  box-sizing: border-box;
  min-height: 100vh;
  position: relative;
}

#header .logo {
  animation-name: logointro;
  animation-iteration-count: 1;
  animation-timing-function: ease-out;
  animation-duration: .6s;
}

#header p {
  animation-name: headertextintro;
  animation-iteration-count: 1;
  animation-timing-function: ease-out;
  animation-duration: 1.6s;
}

@keyframes logointro {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes headertextintro {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

#scroller {
  position: absolute;
  bottom: 6em;
  left: 50%;
  cursor: pointer;
  z-index: 2;
}

#scroller span {
  position: absolute;
  top: 0;
  left: 50%;
  width: 46px;
  height: 46px;
  margin-left: -23px;
  box-sizing: border-box;
}

#scroller span::after {
  position: absolute;
  top: 50%;
  left: 50%;
  content: '';
  width: 16px;
  height: 16px;
  margin: -12px 0 0 -8px;
  border-left: 2px solid #fff;
  border-bottom: 2px solid #fff;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  box-sizing: border-box;
}

#scroller span::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  content: '';
  width: 46px;
  height: 46px;
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.1);
  border-radius: 100%;
  opacity: 0;
  animation: scrollindicator 2s infinite;
  box-sizing: border-box;
}

@keyframes scrollindicator {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  60% {
    box-shadow: 0 0 0 20px rgba(255, 255, 255, 0.1);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

#featured a img {
  height: 2.5em;
}

#testimonials {
  background-color: #f8fafc;
}

#testimonials a {
  display: inline-block;
  margin-bottom: 1.625em;
}

#testimonials a img {
  display: block;
  height: 75px;
}

@media screen and (min-width: 31.5rem) {
  #testimonials a img {
    height: 100px;
  }
}

@media screen and (min-width: 61.5rem) {
  #testimonials a img {
    height: 125px;
  }
}

#testimonials ul {
  position: relative;
  margin: 0;
  padding: 0;
  height: 6em;
  list-style: none;
}

@media screen and (min-width: 31.5rem) {
  #testimonials ul {
    height: 4em;
  }
}

#testimonials li {
  position: absolute;
  top: 50%;
  width: 100%;
  margin: 0;
  padding: 0;
  opacity: 0;
  text-align: center;
  font-size: 1em;
  font-style: italic;
  animation: rotateWordsFirst 30s linear infinite 0s;
  transform: translateY(-50%);
}

#testimonials li:nth-child(2) {
  animation-delay: 5s;
}

#testimonials li:nth-child(3) {
  animation-delay: 10s;
}

#testimonials li:nth-child(4) {
  animation-delay: 15s;
}

#testimonials li:nth-child(5) {
  animation-delay: 20s;
}

#testimonials li:nth-child(6) {
  animation-delay: 25s;
}

@media screen and (min-width: 61.5rem) {
  #testimonials li {
    font-size: 1.5em;
  }
}

@keyframes rotateWordsFirst {
  0% {
    opacity: 0;
    animation-timing-function: ease-in;
  }
  4% {
    opacity: 1;
  }
  12% {
    opacity: 1;
  }
  18% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

